export default {
  name: "region_list",
  props: {
    region: undefined,
    type: undefined,
    basepath:undefined
  },
  data() {
    return {
      provinces_list: undefined,
      city_list: undefined,
      province: undefined,
      target_region: undefined,
    }
  },
  destroyed() {
    this.listener_init.off();
  },
  created() {
    if (!this.target_region) {
      this.target_region = {
        province: undefined
      };
    }

    this.listener_init = this.$hub.on('event:init', () => {
      this.update_data();
      this.$forceUpdate();
    });
    this.update_data();
  },
  methods: {
    on_provinces_click: function (provinces) {
      if(!this.basepath){
        this.basepath = this.$route.path;
      }
      var url = this.basepath;
      if (this.type) {
        url = this.updateQueryStringParameter(url, 'type', this.type);
      }
      url = this.updateQueryStringParameter(url, 'r', provinces);
      this.$router.push(url);
    },
    update_data: function () {

      try {
        if (this.province) {
          this.city_list = this.$store.state.init.data.region_conf[this.province];
        }
        if (!this.city_list && this.region) {
          this.city_list = this.$store.state.init.data.region_conf[this.region];
          if (this.city_list) {
            this.province = this.region;
          }
          console.log('[update_data] province:', this.province);
          if (this.target_region && this.target_region.province) {
            this.city_list = this.$store.state.init.data.region_conf[this.target_region.province];
            if (this.city_list) {
              this.province = this.target_region.province;
            }
          }
        }
      } catch (e) {

      }

      try {
        this.provinces_list = this.$store.state.init.data.provinces_list;
        console.log('provinces_list:', this.provinces_list);
        console.log('target_region:', this.target_region);
      } catch (e) {

      }
      this.$forceUpdate();
    }
  }
}
