import region_list from '@/components/region/list'
import cha_list from "@/components/cha/item/cha_list";

export default {
  name: "index",
  components: {
    cha_list,
    region_list
  },
  created() {
    this.listener_init = this.$hub.on('event:init', () => {
      console.log('call index event:init');
      this.init();
      this.$forceUpdate();
    });
    this.init();
  },
  data() {
    return {
      index_datas: undefined
    }
  },
  methods: {
    init: function () {
      try {
        this.index_datas = this.$store.state.init.data.index_datas.index_datas;
      } catch (e) {

      }
    },

  },
  computed: {},
  destroyed() {
    this.listener_init.off();
  },
}
